.number-field {
    height: 2rem;
    background-color: #282c34;
    border: 2px solid #09d3ac;
    border-bottom: 2px solid #09d3ac;
    box-shadow: -1px 0px 0 4px #282c34, -6px 5px 0 2px #09d3ac;
}

.number-input {
    width: 53%;
    height: 1.7rem;
    color: white;
    font-size: 1.2rem;
    text-align: center;
    vertical-align: 2px;
    background-color: transparent;
    border: 2px solid transparent;
}

.number-button {
    font-size: 1.3rem;
    color: white;
    background-color: #282c34;
    border: 0px solid transparent;
    cursor: pointer;
    transition: 0.1s linear;
}

.number-button:hover {
    background: #09d3ac;
    color: #282c34;
}

.number-button.left {
    float: left;
    border-right: 2px solid #09d3ac;
    padding-left: 12px;
    padding-right: 12px;
}

.number-button.right {
    float: right;
    border-left: 2px solid #09d3ac;
    padding-left: 10px;
    padding-right: 10px;
}

.number-button.left:active,
.number-button.right:active {
    background-color: #0c9b7e;
}